.footer {
  background-color: $black2;
  padding: 97px 98px 77px;

  display: flex;
  flex-shrink: 0;
}

.footer__container {
  width: 100%;
  max-width: 1170px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

.footer--catalog {
  position: relative;
}

.footer--catalog::before {
  content: "";
  display: block;
  position: absolute;
  top: -130px;
  left: -98px;
  width: calc(100% + 87px);
  height: 0;
  border: 1px solid $grey2;
}

.footer .sign__input {
  margin-top: 25px;
  color: $base-color;
}

.footer__wrapper {
  display: flex;
}

.footer .nav__list {
  margin-left: 95px;
}

.footer .nav__list--about {
  margin-left: 90px;
}

.footer__contacts {
  width: 115px;
  margin-left: 87px;
}

@media (max-width: $desktop-width) {
  .footer {
    padding-top: 55px;
    padding-left: 45px;
  }

  .footer__container {
    flex-direction: column;
  }

  .footer__wrapper {
    display: flex;
  }

  .footer .nav__list {
    width: max-content;
    margin-left: 0;
    margin-right: 87px;
  }

  .footer__contacts {
    margin-left: 0;
  }
}

@media (max-width: $tablet-width) {
  .footer {
    padding: 50px 10px 34px 13px;
  }

  .footer__wrapper {
    flex-direction: column;
  }

  .footer .sign__input {
    margin-top: 24px;
  }

  .footer .nav__list {
    margin-bottom: 27px;
  }

  .footer .contacts__list {
    display: flex;
  }

  .footer .contacts__title {
    display: none;
  }

  .footer .contacts__icon {
    margin-right: 30px;
  }

  .footer .contacts__icon--facebook {
    width: 12px;
    height: 21px;
  }

  .footer .contacts__icon--instagram,
  .footer .contacts__icon--pinterest {
    width: 20px;
    height: 21px;
  }

  .footer .contacts__icon--twitter {
    width: 20px;
    height: 18px;
  }

  .footer--catalog::before {
    display: none;
  }
}
