.article {
  display: flex;
}

.article__desc {
  box-sizing: border-box;
  width: 285px;
  height: 300px;
  padding-top: 11%;
  padding-left: 10%;
  padding-right: 10%;
  background-color: $beige;
}

.article__desc--reversed {
  order: -1;
}

.article p {
  margin: 0;
  color: $grey;
  font-size: 14px;
}

.article h3 {
  margin-top: 12px;
  margin-bottom: 36px;
  font-family: $ff-secondary;
  font-size: 26px;
  line-height: 34px;
  color: $black2;
}

.article__link {
  color: $black;
  text-decoration: none;
  border-bottom: 1px solid $sandybrown;

  &:hover {
    color: $sandybrown;
  }
}

.article--big {
  height: 630px;
  float: right;
  margin-top: -660px;
  position: relative;
}

.article__img {
  width: 285px;
  height: 300px;
}

.article--big .article__img {
  width: 570px;
  height: 630px;
}

.article--big .article__desc {
  height: 315px;
  padding-top: 90px;
  position: absolute;
  bottom: 0;
  right: -1px;
}

@media (max-width: $desktop-width) {
  .article__desc {
    flex-grow: 1;
    width: 339px;
    height: 300px;
    padding-left: 88px;
    padding-right: 85px;
  }

  .article__img {
    flex-grow: 1;
    width: 339px;
    height: 300px;
  }

  .article--big {
    height: auto;
    float: none;
    margin-top: 50px;
  }

  .article--big img {
    max-height: 300px;
  }

  .article--big .article__img {
    width: 339px;
    height: 300px;
  }

  .article--big .article__desc {
    position: static;
    height: 300px;
    padding-top: 80px;
  }
}

@media (max-width: $tablet-width) {
  .article {
    flex-direction: column;
  }

  .article__img,
  .article--big .article__img {
    width: inherit;
    height: 256px;
  }

  .article--big {
    margin-top: 0;
  }

  .article__desc {
    width: inherit;
    height: 256px;
    padding-top: 45px;
    padding-left: 60px;
    padding-right: 60px;
  }

  .article--big .article__desc {
    padding-top: 60px;
    height: 256px;
  }

  .article__desc--reversed {
    order: initial;
  }
}
