.main {
  flex: 1 0 auto;
}

.main__wrapper {
  width: 87%;
  max-width: 1170px;
  margin-right: auto;
  margin-left: auto;
}

.main__wrapper--catalog {
  display: flex;
}

.main .tagline--catalog {
  margin-top: 0;
}

.main .slider {
  margin-top: 120px;
}

.main .blog {
  margin-top: 112px;
}

.main .questions {
  margin-top: 120px;
}

.main .insta-tag {
  margin-top: 119px;
  margin-bottom: 130px;
}

.main__filters-btn {
  display: none;
}

.main .filters {
  margin-top: 80px;
}

.main .catalog {
  margin-top: 80px;
  margin-left: 50px;
  margin-bottom: 72px;
}

.main .product {
  margin-top: 80px;
}

.main--product .main__slider {
  margin-top: 89px;
  margin-bottom: 122px;
}

@media (max-width: $desktop-width) {
  .main__wrapper {
    width: 88%;
  }

  .main__wrapper--catalog {
    flex-direction: column;
  }

  .main__filters-btn {
    display: block;
    width: 100%;
    margin-top: 40px;
  }

  .main .slider {
    margin-top: 70px;
  }

  .main .blog {
    margin-top: 46px;
  }

  .main .questions {
    margin-top: 70px;
  }

  .main .insta-tag {
    margin-top: 75px;
    margin-bottom: 80px;
  }

  .main .catalog {
    margin-top: 50px;
    margin-left: 0;
  }

  .main .tagline--catalog {
    margin-top: 11px;
  }

  .main .product {
    margin-top: 31px;
  }

  .main--product .slider {
    margin-top: 40px;
    margin-bottom: 56px;
  }
}

@media (max-width: $tablet-width) {
  .main__wrapper {
    width: 90.6%;
  }

  .main .tagline--catalog {
    margin-top: 0;
  }

  .main .slider {
    margin-top: 60px;
  }

  .main .blog {
    margin-top: 109px;
  }

  .main .questions {
    margin-top: 60px;
  }

  .main .insta-tag {
    margin-top: 60px;
    margin-bottom: 70px;
  }

  .main .catalog {
    margin-top: 40px;
    margin-bottom: 73px;
  }

  .main .product {
    margin-top: 0;
  }

  .main--product .slider {
    margin-top: 56px;
    margin-bottom: 50px;
  }
}
